import { AppRegistry, LogBox } from 'react-native';
import { LoadSkiaWeb } from '@shopify/react-native-skia/lib/module/web';

/* import { enableExperimentalWebImplementation } from 'react-native-gesture-handler';
enableExperimentalWebImplementation(true); */

// FIXME need reanimated update, see https://github.com/software-mansion/react-native-reanimated/issues/3355
// @ts-ignore
window._frameTimestamp = null;

// Generate required css
import iconFont from './app/assets/fonts/icomoon.ttf';
import introFont from './app/assets/fonts/Intro-Bold-Alt.ttf';

const iconFontStyles = `
@font-face {
  src: url(${iconFont});
  font-family: icomoon;
}
@font-face {
  src: url(${introFont});
  font-family: Intro-Bold-Alt;
}`;

// Create stylesheet
const style = document.createElement('style');
style.type = 'text/css';
if (style.styleSheet) {
  style.styleSheet.cssText = iconFontStyles;
} else {
  style.appendChild(document.createTextNode(iconFontStyles));
}

// Inject stylesheet
document.head.appendChild(style);

LogBox.ignoreAllLogs();

export default function registerRootComponent() {
  LoadSkiaWeb({
    locateFile: (file) => `https://cdn.jsdelivr.net/npm/canvaskit-wasm@0.37.0/bin/full/${file}`,
  }).then(async () => {
    const App = (await import('./app/WebApp')).default;
    AppRegistry.registerComponent('LandalParkApp', () => App);
    AppRegistry.runApplication('LandalParkApp', {
      rootTag: document.getElementById('root'),
    });
  });
}
registerRootComponent();
